@import url('https://fonts.googleapis.com/css?family=Alfa+Slab+One%7CGentium+Book+Basic:400,400i');

.App {
  text-align: center;
  font-family: "Helvetica", serif;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

h1 {
  font: 60px/75px "Alfa Slab One", cursive, sans-serif;
  text-transform: uppercase;
}

h3 {
  font:  400 30px/40px "Helvetica", serif;
}
.navLinks {
  list-style: none;
  padding-left: 0;
}

.navLinks li {
  display: inline-block;
  padding: 0 15px;
}

.navLinks a {
  color: #02b1ff;
  text-decoration: none;
  font-size: 25px;
}

.navLinks a.active {
  color: #F79824;
}

.navLinks a.active:hover {
  color: #FDCA40;
}

.navLinks a:hover {
  color: #0184be;
} 


